.center {
  width: 100%;
  height: 100vh;
}

.container {
  position: relative;
  top: 15%;
}

.submit {
  text-align: center;
}

.submitbutton {
  position: relative;
  left: 42%;
  cursor: pointer !important;
}

.todobutton {
  margin-left: 5px;
  margin-top: 5px;
  cursor: default !important;
}

@media only screen and (max-width: 576px) {
  .jumbotron  {
      padding-bottom: 2.8rem;
  }
  .submitbutton {
    position: relative;
    left: 40%;
  }
}


